this["App"]["Templates"]["demo"]["app"]["styleGuide"]["styleGuideTemplate"] = Handlebars.template({"compiler":[7,">= 4.0.0"],"main":function(container,depth0,helpers,partials,data) {
    return "\r\n\r\n<div id=\"contenedor\">\r\n  <section>\r\n    <h2 class=\"page-header\">Tipografa</h2>\r\n\r\n\r\n    <p>El tema por defecto de UDA define una serie de estilos predeterminados para las aplicaciones.</p>\r\n\r\n    <p>As pues, se consideran los siguientes valores:</p>\r\n\r\n    <ul>\r\n      <li><code>font-family</code> : El tipo de letra utilizado por defecto es \"Open Sans\".</li>\r\n      <li><code>font-size</code> : Se establece un valor de <strong>12px</strong> para el tamao de letra</li>\r\n    </ul>\r\n\r\n    <div class=\"example\">\r\n      <h1>h1. Encabezado <small>Texto secundario</small></h1>\r\n      <h2>h2. Encabezado <small>Texto secundario</small></h2>\r\n      <h3>h3. Encabezado <small>Texto secundario</small></h3>\r\n      <h4>h4. Encabezado <small>Texto secundario</small></h4>\r\n      <h5>h5. Encabezado <small>Texto secundario</small></h5>\r\n      <h6>h6. Encabezado <small>Texto secundario</small></h6>\r\n    </div>\r\n\r\n  </section>\r\n\r\n  <section>\r\n    <h2 class=\"page-header\">Botones</h2>\r\n\r\n    <p>Se determina una misma apariencia para los botones nativos HTML, de bootstrap y UDA.</p>\r\n\r\n    <p>As pues la apariencia de los botones nativos <code>button</code>, <code>&lt;input type=\"text\" /&gt;</code> y <code>&lt;input type=\"submit\" /&gt;</code>sera:</p>\r\n\r\n    <div class=\"example\">\r\n      <div class=\"row\">\r\n        <div class=\"col-md-12\">\r\n          <button class=\"\" type=\"submit\">Submit</button>\r\n          <button class=\"\" >Botn defecto</button>\r\n          <input type=\"button\" value=\"Input button\" />\r\n          <input type=\"submit\" value=\"Input submit\" />\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <p>Los botones de bootstrap <code>.btn</code> se visualizaran del siguiente modo:</p>\r\n\r\n    <div class=\"example\">\r\n      <div class=\"row\">\r\n        <div class=\"col-md-12\">\r\n          <!-- Standard button -->\r\n          <button type=\"button\" class=\"btn btn-primary\">Principal</button>\r\n\r\n          <!-- Provides extra visual weight and identifies the primary action in a set of buttons -->\r\n          <button type=\"button\" class=\"btn btn-secondary\">Secundario</button>\r\n\r\n          <!-- Indicates a successful or positive action -->\r\n          <button type=\"button\" class=\"btn btn-success\">Success</button>\r\n\r\n          <!-- Contextual button for informational alert messages -->\r\n          <button type=\"button\" class=\"btn btn-info\">Info</button>\r\n\r\n          <!-- Indicates caution should be taken with this action -->\r\n          <button type=\"button\" class=\"btn btn-warning\">Warning</button>\r\n\r\n          <!-- Indicates a dangerous or potentially negative action -->\r\n          <button type=\"button\" class=\"btn btn-danger\">Danger</button>\r\n\r\n          <!-- Deemphasize a button by making it look like a link while maintaining button behavior -->\r\n          <button type=\"button\" class=\"btn btn-link\">Link</button>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <p>Este sera el aspecto de los botones <code>rup_button</code> de UDA:</p>\r\n\r\n    <div class=\"example\">\r\n      <div class=\"row\">\r\n        <div class=\"col-md-12\">\r\n          <!-- Standard button -->\r\n          <button type=\"button\" class=\"rup-button\">Default</button>\r\n\r\n          <button type=\"button\" id=\"dropdownHtmlListButton\">Lista HTML</button>\r\n          <ul id=\"dropdownHtmlList\" class=\"rup-dropdown-option-list\">\r\n          	<li><a href=\"#\" id=\"dropdownElem1\">Elemento 1</a></li>\r\n          	<li><a href=\"#\" id=\"dropdownElem2\">Elemento 2</a></li>\r\n          	<li><a href=\"#\" id=\"dropdownElem3\">Elemento 3</a></li>\r\n          	<li class=\"divider\"></li>\r\n          	<li><a href=\"#\" id=\"dropdownElem4\">Elemento 4</a></li>\r\n          </ul>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </section>\r\n\r\n  <section>\r\n    <h2 class=\"page-header\">Formulario</h2>\r\n\r\n    <p>El tema de UDA proporciona una apariencia unificada de los diferentes elementos que pueden estar presentes en un formulario.</p>\r\n\r\n    <h3>Campos de texto</h3>\r\n\r\n    <p>Se busca normalizar la apariencia de los campos de texto por defecto as como de los que hacen uso de los estilos de bootstrap.</p>\r\n    <p>Un campo de texto por defecto hace uso de las siguientes propiedades de css:</p>\r\n\r\n    <ul>\r\n      <li><code>font-family</code> : El tipo de letra utilizado por defecto es \"Open Sans\".</li>\r\n      <li><code>font-size</code> : Se establece un valor de <strong>1rem</strong> para el tamao de letra. Como es relativo al tamao base definido para <code>html</code> el valor por defecto ser de <strong>12px</strong>.</li>\r\n      <li><code>height</code> : Se establece un valor de <strong>2rem</strong> para la altura del campo de texto. Como es relativo al tamao base definido para <code>html</code> el valor por defecto ser de <strong>24px</strong>.</li>\r\n    </ul>\r\n\r\n    <div class=\"example\">\r\n      <label for=\"textFieldDefault\">Campo de texto por defecto</label>\r\n      <input type=\"text\" id=\"textFieldDefault\" placeholder=\"default\"/>\r\n    </div>\r\n\r\n    <p>En caso de utilizar los estilos de bootstrap a la hora de disear los campos de los formularios el estilo ser similar.</p>\r\n\r\n    <div class=\"example\">\r\n      <div class=\"form-group\">\r\n        <label for=\"textFieldBootstrap\">Campo de texto Bootstrap</label>\r\n        <input type=\"text\" class=\"form-control\" id=\"textFieldBootstrap\" placeholder=\".form-control\">\r\n      </div>\r\n      <div class=\"form-group\">\r\n        <label for=\"passwordFieldBootstrap\">Password</label>\r\n        <input type=\"password\" class=\"form-control\" id=\"passwordFieldBootstrap\" placeholder=\"Password\">\r\n      </div>\r\n    </div>\r\n\r\n    <h4>Tamaos</h4>\r\n\r\n    <p>Mediante determinadas clases es posible modificar el tamao de los campos de entrada.</p>\r\n\r\n    <div class=\"example\">\r\n      <input type=\"text\" class=\"form-control form-control-lg\" id=\"textFieldBootstrap\" placeholder=\".form-control-lg\">\r\n      <input type=\"text\" class=\"form-control\" id=\"textFieldBootstrap\" placeholder=\"Tamao por defecto\">\r\n      <input type=\"text\" class=\"form-control form-control-sm\" id=\"textFieldBootstrap\" placeholder=\".form-control-sm\">\r\n      form-control-lg\r\n    </div>\r\n\r\n\r\n    <h3>Select</h3>\r\n\r\n    <p>Del mismo modo se trata de obtener una apariencia similar en el uso de los controles <code>select</code>.</p>\r\n\r\n    <p>Las propiedades aplicadas a los estilos son:</p>\r\n\r\n    <ul>\r\n      <li><code>font-family</code> : El tipo de letra utilizado por defecto es \"Open Sans\".</li>\r\n      <li><code>font-size</code> : Se establece un valor de <strong>1rem</strong> para el tamao de letra. Como es relativo al tamao base definido para <code>html</code> el valor por defecto ser de <strong>12px</strong>.</li>\r\n      <li><code>height</code> : Se establece un valor de <strong>2rem</strong> para la altura del combo. Como es relativo al tamao base definido para <code>html</code> el valor por defecto ser de <strong>24px</strong>.</li>\r\n    </ul>\r\n\r\n    <p>As pus la apariencia de los diferentes combos que pueden ser incluidos en el formulario es la siguiente:</p>\r\n\r\n    <p>Combo por defecto:</p>\r\n\r\n    <div class=\"example\">\r\n      <select  id=\"selectDefault\">\r\n        <option>Opcin 1</option>\r\n        <option>Opcin 2</option>\r\n        <option>Opcin 3</option>\r\n        <option>Opcin 4</option>\r\n        <option>Opcin 5</option>\r\n      </select>\r\n    </div>\r\n\r\n    <p>Combo Bootstrap:</p>\r\n\r\n    <div class=\"example\">\r\n      <select  id=\"selectBootstrap\" class=\"form-control\">\r\n        <option>Opcin 1</option>\r\n        <option>Opcin 2</option>\r\n        <option>Opcin 3</option>\r\n        <option>Opcin 4</option>\r\n        <option>Opcin 5</option>\r\n      </select>\r\n    </div>\r\n\r\n    <p>Combo UDA:</p>\r\n\r\n    <div class=\"example\">\r\n      <select  id=\"rupCombo\">\r\n        <option>Opcin 1</option>\r\n        <option>Opcin 2</option>\r\n        <option>Opcin 3</option>\r\n        <option>Opcin 4</option>\r\n        <option>Opcin 5</option>\r\n      </select>\r\n    </div>\r\n  </section>\r\n</div>\r\n";
},"useData":true});
