this["App"]["Templates"]["demo"]["app"]["components"]["toolbar"]["toolbarTestTemplate"] = Handlebars.template({"compiler":[7,">= 4.0.0"],"main":function(container,depth0,helpers,partials,data) {
    return "<h3>Botonera</h3>\r\n<p>\r\n  Se les presenta a los usuarios una barra de botones con diversas funcionalidades relacionadas a elementos de la pgina. Gracias a este componente se presentan, ordenan y agrupan las distintas funcionalidades gestionadas por las aplicaciones.\r\n</p>\r\n<p>\r\n  La configuracin de la botonera puede realizarse mediante un json en el que indican los diferentes botones que se van a incluir o directamente mediante elementos HTML.\r\n</p>\r\n<p>\r\n  Un ejemplo de construccin de una botonera a partir de un json sera la siguiente:\r\n</p>\r\n<div class=\"example\">\r\n  <div id=\"toolbar\"></div>\r\n</div>\r\n\r\n<p>\r\n  En el json de definicin de los botones es posible especificar que  ciertos botones se agrupen a la izquierda de la botonera.\r\n</p>\r\n<p>\r\n  Para ello, se debe de hacer uso de la propiedad <code>right: true</code> en la definicin del botn.\r\n</p>\r\n<div class=\"example\">\r\n  <div id=\"toolbarRight\"></div>\r\n</div>\r\n\r\n<p>\r\n  Naturalmente, es posible combinar botones alineados tanto a la derecha como a la izquierda.\r\n</p>\r\n<div class=\"example\">\r\n  <div id=\"toolbarMixta\"></div>\r\n</div>\r\n\r\n<p>\r\n  Configruacin de mbuttons responsive\r\n</p>\r\n<div class=\"example\">\r\n  <div id=\"toolbarRwd\"></div>\r\n</div>\r\n\r\n\r\n\r\n<h4>Compatibilidad jQueryUI</h4>\r\n<p>\r\n  El componente proporciona compatibilidad con las configuraciones y estilos utilizados en las aplicaciones UDA anteriores a la versin v3.0.0.\r\n</p>\r\n<p>\r\n  En el caso de que se quiera inicializar un componente Toolbar con el modo de compatibilidad activado, se debera:\r\n</p>\r\n<ul>\r\n  <li>Utilizar la propiedad <code>adapter: \"toolbar_jqueryui\"</code> en la inicializacin del componente.</li>\r\n  <li>Indicar la propiedad <code>data-adapter=\"toolbar_jqueryui\"</code> en el <code>&lt;div&gt;</code> sobre el que se inicializa el componente.</li>\r\n</ul>\r\n<p>Este es un ejemplo de las posibles configuraciones:</p>\r\n\r\n<div class=\"example\">\r\n\r\n  <div id=\"jQueryUIToolbar\"></div>\r\n  <br/>\r\n  <div class=\"separator\"></div>\r\n  <div class=\"separator\"></div>\r\n  <br/>\r\n  <div id=\"jQueryUIToolbarMixta\"></div>\r\n  <br/>\r\n  <div class=\"separator\"></div>\r\n  <div class=\"separator\"></div>\r\n  <br/>\r\n  <div id=\"jQueryUIToolbarRight\"></div>\r\n\r\n</div>\r\n\r\n\r\n<!-- <div id=\"toolbarNew\" class=\"rup-toolbar\">\r\n\r\n  <button type=\"button\"class=\"btn btn-secondary\">\r\n    <i class=\"fa fa-search\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Buscar</span>\r\n  </button>\r\n  <button type=\"button\"class=\"btn btn-secondary\">\r\n    <i class=\"fa fa-pencil-square-o\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Editar</span>\r\n  </button>\r\n  <button type=\"button\"class=\"btn btn-secondary\">\r\n    <i class=\"fa fa-trash-o\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Borrar</span>\r\n  </button>\r\n  <button type=\"button\"class=\"btn btn-secondary\">\r\n    <i class=\"fa fa-filter\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Filtrar</span>\r\n  </button>\r\n\r\n  <button type=\"button\"class=\"btn btn-secondary rup-button-right rup-toolbar_menuButton\" data-mbutton=\"ficherosMButton\">\r\n    <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Ficheros</span>\r\n  </button>\r\n  <div class=\"rup-toolbar-mbutton\" id=\"ficherosMButton\">\r\n    <li>\r\n      <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n        <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n        <span class=\"rup-ui-button-text\">Ficheros</span>\r\n      </button>\r\n    </li>\r\n    <li>\r\n      <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n        <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n        <span class=\"rup-ui-button-text\">Ficheros</span>\r\n      </button>\r\n    </li>\r\n    <li>\r\n      <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n        <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n        <span class=\"rup-ui-button-text\">Ficheros</span>\r\n      </button>\r\n    </li>\r\n    <li>\r\n      <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n        <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n        <span class=\"rup-ui-button-text\">Ficheros</span>\r\n      </button>\r\n    </li>\r\n  </ul>\r\n\r\n\r\n  <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n    <i class=\"fa fa-print\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Imprimir</span>\r\n  </button>\r\n  <button type=\"button\"class=\"btn btn-secondary rup-button-right\">\r\n    <i class=\"fa fa-times-circle\" aria-hidden=\"true\"></i>\r\n    <span class=\"rup-ui-button-text\">Cancelar</span>\r\n  </button>\r\n\r\n\r\n\r\n</div> -->\r\n";
},"useData":true});
